.container {
  max-width: 820px;
  width: 80%;
  margin: 0 auto;
  @media (max-width: 991px) {
    width: 90%;
  }
  @media (max-width: 767px) {
    width: 100%;
  }
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    @media (max-width: 575px) {
      justify-content: center;
    }
    .basket {
      display: flex;
      align-items: center;
      gap: 15px;
      font-size: 32px;
      font-weight: 700;
      @media (max-width: 767px) {
        font-size: 24px;
      }
      @media (max-width: 575px) {
        display: none;
      }
    }
    .clearBasket {
      height: 20px;
      display: flex;
      align-items: center;
      gap: 7px;
      font-size: 16px;
      font-weight: 400;
      color: #b6b6b6;
      cursor: pointer;
      &:hover {
        color: black;
      }
    }
  }
  .total {
    display: flex;
    justify-content: space-between;
    .totalQ {
      font-weight: 700;
    }
    .totalP {
      font-weight: 700;
      color: #fe5f1e;
    }
  }
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    @media (max-width: 575px) {
      flex-direction: column-reverse;
      gap: 20px;
    }
  }
}

.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  .title {
    font-weight: 700;
    font-size: 30px;
    text-transform: uppercase;
  }
  .text {
    margin: 15px 0;
    text-align: center;
    color: #777777;
  }
}
