.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  border-top: 1px solid #f4f4f4;
  .pizza {
    display: flex;
    align-items: center;
    flex-grow: 1;
    .pizzaIMG {
      height: 80px;
      margin-right: 10px;
      @media (max-width: 767px) {
        height: 60px;
      }
      @media (max-width: 575px) {
        height: 40px;
        margin-right: 5px;
      }
    }
    h3 {
      @media (max-width: 575px) {
        font-size: 14px;
      }
    }
    .info {
      display: flex;
      flex-direction: column;
      gap: 5px;
      .description {
        font-size: 18px;
        @media (max-width: 767px) {
          font-size: 14px;
        }
        @media (max-width: 575px) {
          font-size: 10px;
        }
      }
    }
  }
  .price {
    min-width: 10%;
    text-align: center;
    font-size: 22px;
    margin: 0 7%;
    @media (max-width: 767px) {
      font-size: 18px;
      margin: 0 5%;
    }
    @media (max-width: 575px) {
      font-size: 14px;
      margin: 0 2%;
    }
  }
  .deletePizza {
    color: #b6b6b6;
    cursor: pointer;
    @media (max-width: 575px) {
      width: 20px;
      height: 20px;
    }
    &:hover {
      color: black;
    }
  }
}
