.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 991px) {
    flex-direction: column;
    gap: 30px;
  }

  .categories {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    @media (max-width: 575px) {
      justify-content: center;
      gap: 10px;
      font-size: 12px;
    }

    .category {
      font-weight: 700;

      display: flex;
      align-items: center;

      height: 46px;
      padding: 0 20px;

      background: #f9f9f9;
      border-radius: 30px;

      cursor: pointer;

      @media (max-width: 767px) {
        border-radius: 20px;
      }
      @media (max-width: 575px) {
        height: 36px;
        padding: 0 10px;

        border-radius: 10px;
      }
    }

    .active {
      background: black;
      color: white;
    }
  }

  .sortByCriterion {
    position: relative;

    width: 205px;
    .chosenSort {
      color: #fe5f1e;
      text-decoration: underline;
      cursor: pointer;
    }
    .popup {
      position: absolute;
      top: 30px;
      left: 50px;

      padding: 5px 10px;

      border: 2px solid;
      border-radius: 10px;
      background-color: rgb(240, 240, 240);

      .popupElem {
        display: flex;
        align-items: center;

        width: 130px;
        height: 35px;

        cursor: pointer;
      }
      .active {
        background: rgba(254, 95, 30, 0.05);
        color: #fe5f1e;
      }
    }
  }
}
