.button {
  padding: 10px;
  border: 2px solid currentColor;
  background-color: #fff;
  border-radius: 20px;
  font-weight: 700;
  color: #eb5a1e;
  cursor: pointer;
  font-size: 16px;
  &:hover {
    background-color: #fe5f1e;
    color: white;
  }
  @media (max-width: 400px) {
    padding: 5px;
    border-radius: 10px;
  }
}
.grey {
  color: #b6b6b6;
  &:hover {
    color: black;
    background-color: transparent;
  }
}
.disabled {
  pointer-events: none;
  opacity: 0.5;
}
