.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: rgba(0, 0, 0, 0.3);

  .modalWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;

    width: max-content;
    padding: 10px 20px 20px 20px;
    border-radius: 15px;
    background-color: #fff;

    @media (max-width: 575px), (max-height: 500px) {
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      transform: none;

      width: 100%;
      height: 100%;

      border-radius: 0;
    }

    .close {
      cursor: pointer;
      align-self: flex-end;
    }
    .list {
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin: 10px 0;
    }
    .ol {
      padding-inline-start: 20px;
    }
    form {
      margin: 10px 0;
      .adresWrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 0 15px;
        @media (max-height: 500px) {
          grid-template-columns: 1fr 1fr 1fr 1fr;
        }
      }
      .full {
        grid-column: 1/3;
        @media (max-height: 500px) {
          grid-column: 1/5;
        }
      }
      input {
        width: 100%;
        height: 30px;
        border: none;
        outline: 1px solid #b6b6b6;
        border-radius: 5px;
        padding-left: 10px;
      }
      label {
        display: block;
        margin-bottom: 15px;
        position: relative;
        &:last-child {
          margin-bottom: 0;
        }
        .label {
          display: block;
          margin-bottom: 5px;
          min-width: 150px;

          @media (max-width: 575px), (max-height: 500px) {
            min-width: auto;
          }
        }
        .required {
          color: red;
        }
        .uncorrect {
          position: absolute;
          bottom: 0;
          left: 0;
          color: red;
          transform: translateY(120%);
          margin-bottom: 0;
          font-size: 12px;
        }
      }
    }
  }
}

