.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 50px;

  @media (max-width: 600px) {
    flex-direction: column;
    gap: 20px;

    margin-bottom: 20px;
  }

  .logo {
    max-width: 100%;
  }

  .basketButton {
    position: relative;
    display: flex;
    .devider {
      position: absolute;
      left: 79.5px;
      top: 12px;

      width: 1px;
      height: 26px;

      background: rgba(255, 255, 255, 0.5);
    }
    .half {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;

      width: 80px;
      height: 50px;

      font-size: 20px;
      font-weight: 700;

      color: #ffffff;
      background-color: #fe5f1e;
    }
    .left {
      border-top-left-radius: 30px;
      border-bottom-left-radius: 30px;
    }
    .right {
      border-top-right-radius: 30px;
      border-bottom-right-radius: 30px;
    }
  }
}
