.pizzas {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(275px, 1fr));
  gap: 15px;
  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.card {
  font-weight: 700;
  @media (max-width: 767px) {
    display: flex;
    align-items: center;
  }
  .imgBlock {
    margin-bottom: 15px;
    text-align: center;
    @media (max-width: 767px) {
      flex-basis: 30%;
      margin-bottom: 0;
    }
    @media (max-width: 400px) {
      flex-basis: 20%;
    }
    .img {
      width: 100%;
    }
    .desktopeName {
      @media (max-width: 767px) {
        display: none;
      }
    }
  }
  .valuesBlock {
    @media (max-width: 767px) {
      flex-grow: 1;
    }
    .mobileName {
      display: none;
      @media (max-width: 767px) {
        display: block;
        text-align: center;
        margin-bottom: 5px;
        font-size: 20px;
      }
    }
    .values {
      background: #f3f3f3;
      border-radius: 10px;
      margin-bottom: 10px;
      @media (max-width: 400px) {
        margin-bottom: 5px;
      }
      .valueContainer {
        display: flex;
        height: 40px;
        padding: 5px 10px;
        &:first-child {
          padding: 5px 10px 2.5px 10px;
        }
        &:last-child {
          padding: 2.5px 10px 5px 10px;
        }
        @media (max-width: 400px) {
          height: 30px;
        }
      }
      .item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        cursor: pointer;
      }
      .active {
        background: #ffffff;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
        border-radius: 5px;
      }
    }
  }

  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    .price {
      font-size: 20px;
      min-width: 60px;
      @media (max-width: 575px) {
        font-size: 16px;
        min-width: 50px;
      }
    }
  }
}
