.counter {
  display: flex;
  gap: 5px;
  align-items: center;
  .counterButton {
    color: #fe5f1e;
    cursor: pointer;
    @media (max-width: 400px) {
      width: 20px;
      height: 20px;
    }
    &:hover circle {
      fill: #fe5f1e;
    }
    &:hover path {
      color: white;
    }
  }
  .adaptive {
    @media (max-width: 575px) {
      width: 20px;
      height: 20px;
    }
  }
  .quantity {
    width: 25px;
    text-align: center;
    font-size: 20px;
    @media (max-width: 575px) {
      font-size: 16px;
      width: 20px;
      gap: 0;
    }
  }
}
